@import url('https://fonts.googleapis.com/css2?family=Kanit:wght@300;400;500;600&display=swap');

* {margin:0;padding:0;font-size:14px;font-weight:400;font-family: 'Kanit', 'noto sans KR', sans-serif;}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin:auto;
  width: 100%;
  height: 100vh;
  background:#a1c2ff;
}

.listBox {
  padding:50px;
  max-width:500px;
  width: 90%;
  min-height: 800px;
  background:#FFF;
  border-radius:10px;
  box-shadow:0 0 10px rgba(0,0,0,0.5);
  box-sizing: border-box;
}

h1 {
  font-size:32px;
  font-weight:600;
  margin:auto;
}

.listBox input[type="checkbox"] {
  margin-right:5px;
}

.list {
  padding: 20px;
  border-bottom: 1px solid #CCC;
}

.btnDelete {
  float: right;
  background-color: #FFF;
  color: #777;
  border: none;
  font-size: 13px;
  padding: 5px 7px;
  cursor: pointer;
}

.title {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}

.btnWrap {
  text-align: right;
}

.btnDelAll {
  background:#FFF;
  color:#AAA;
  font-size:12px;
  padding:3px 7px;
  cursor:pointer;
  border:none;
}

.btnDelAll:hover {
  color: rgb(114, 152, 255)
}

form {
  display: flex;
  padding: 20px 0 0 0;
}

.textbox {
  flex:7;
  padding: 4px;
  border:1px solid #88b2ff;
}

.btnSubmit {
  flex: 1.3;
  border: none;
  background:#88b2ff;
  color:#fff;
  cursor:pointer;
}


/* Mobile */
@media only screen and (max-width: 500px) {
  .listBox {
    padding:10vw;
    min-height: 85vh;
    border-radius:2vw;
    box-shadow:0 0 5vw rgba(0,0,0,0.5);
  }

  h1 {
    font-size:8vw;
  }

  .listBox input[type="checkbox"] {
    margin-right:1.5vw;
  }
  
  .textbox {
    padding: 2vw;
  }

  .list {
    padding: 4vw;
    border-bottom: 0.3vw solid #CCC;
  }

  .btnDelete {
    font-size: 3vw;
    padding: 1vw;
  }
  
  .btnDelAll {
    font-size:3vw;
    padding:1vw;
  }
}
